import test from 'ava';
import SearchAlgo from '../index.js';

const searchAlgo = new SearchAlgo;
test('searchAlgo#binarySearch should return "true" if target was found!', t => {
  let arr = [1, 3, 5, 7, 8, 9];
  let x = 5;
  t.is(searchAlgo.binarySearch(arr, x, 0, arr.length-1), true);
});

test('searchAlgo#binarySearch should return "false" if x is not found!', t => {
    let arr = [1, 3, 5, 7, 8, 9];
    let x = 5;
    t.is(searchAlgo.binarySearch(arr, x, 0, arr.length-1), true);
  });
  